// ÐºÐ¾Ñ‚Ð¾Ñ€Ð°Ñ Ð²Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ Ð¿Ñ€Ð¾Ð¼Ð¸Ñ, 
// Ð¿ÐµÑ€ÐµÑ…Ð¾Ð´ÑÑ‰Ð¸Ð¹ Ð² ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ðµ "resolved" Ñ‡ÐµÑ€ÐµÐ· ms Ð¼Ð¸Ð»Ð»Ð¸ÑÐµÐºÑƒÐ½Ð´. Ð—Ð½Ð°Ñ‡ÐµÐ½Ð¸ÐµÐ¼ Ð¸ÑÐ¿Ð¾Ð»Ð½Ð¸Ð²ÑˆÐµÐ³Ð¾ÑÑ Ð¿Ñ€Ð¾Ð¼Ð¸ÑÐ° Ð´Ð¾Ð»Ð¶Ð½Ð¾ Ð±Ñ‹Ñ‚ÑŒ Ñ‚Ð¾ ÐºÐ¾Ð»-Ð²Ð¾ Ð¼Ð¸Ð»Ð»Ð¸ÑÐµÐºÑƒÐ½Ð´ ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ðµ Ð¿ÐµÑ€ÐµÐ´Ð°Ð»Ð¸ Ð²Ð¾ Ð²Ñ€ÐµÐ¼Ñ Ð²Ñ‹Ð·Ð¾Ð²Ð° Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸ delay.

const delay = ms => {
    return new Promise((resolve) => {
        setTimeout(() => {
            resolve(ms);
        }, ms)
    })
  };
  
  const logger = time => console.log(`Resolved after ${time}ms`);
  
  // Ð’Ñ‹Ð·Ð¾Ð²Ñ‹ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸ Ð´Ð»Ñ Ð¿Ñ€Ð¾Ð²ÐµÑ€ÐºÐ¸
  delay(2000).then(logger); // Resolved after 2000ms
  delay(1000).then(logger); // Resolved after 1000ms
  delay(1500).then(logger); // Resolved after 1500ms